		  }
        });
    },
    methods:{
        hhh(){
            
		//学校列表点击事件
		$("div[class='schoolList'] ul li").on("click",function(){
		  $("#schoolName").val($(this).html());
		  $("div[class='provinceSchool']").hide();
	
		});
