	  .provinceSchool .proSelect input { display:none;}
	  .provinceSchool .schoolList {margin-top: 20px; height:200px;margin-left:15px;overflow-y:auto; border:1px solid #72B9D7;}
	  .provinceSchool .schoolList ul { cursor: pointer;}
	  .provinceSchool .schoolList ul li {cursor: pointer; float:left;width:158px;height:22px;margin-left:6px;padding-left:6px;line-height:22px;cursor:pointer;}
		.provinceSchool .schoolList ul li .DoubleWidthLi { width:328px;}
	  .provinceSchool .schoolList ul li:hover { background:#72B9D7;}
	  .provinceSchool .button { width:100%;height:40px;margin-top:8px;}
	  .provinceSchool .button button { float:right;height:30px;margin-right:20px;padding:4px 10px;;border:none;color:#FFF;font-weight:600;cursor:pointer;}</style>
